import bus from './bus.js'
export default {
    data() {
        return {
            msg: ''
        }
    },
    created() {
        uni.$on('cc', this.recive)
        bus.$on('cc', this.recive)
    },
    beforeDestroy() {
        uni.$off('cc',this.recive)
        bus.$off('cc', this.recive)
    },
    methods: {
        recive(e) {
            this.msg = e.msg
        }
    }
}
